div,h2{color:#fff}.go-homepage-button{position:absolute;top:80px;color:rgba(219,69,69,0.61);font-size:25px;margin-left:20px;padding:20px;border:2px solid antiquewhite;border-radius:10px;text-decoration:none;transition:0.15s;will-change:transform}.go-homepage-button:hover{transform:scale(1.05)}.guest-information-message{position:relative;margin:110px 0 0 20px;max-width:250px;border:4px dotted darkcyan;padding:4px;text-align:center;-webkit-animation:banner 2s;animation:banner 2s}@-webkit-keyframes banner{from{transform:scale(0.15)}to{transform:scale(1)}}@keyframes banner{from{transform:scale(0.15)}to{transform:scale(1)}}.user-rated-item-container{display:flex;flex-direction:column}.user-rated-item-container__movies,.user-rated-item-container__tv,.user-rated-item-container__tvepisodes{display:flex;padding:20px;flex-wrap:wrap}.movie-item{opacity:.8;position:relative;text-align:center;transition:transform .2s;text-decoration:none;margin:20px 0 0 20px}.movie-item__image{border-radius:5px;height:150px;width:auto}.movie-item__title{color:rgba(255,255,255,0.9);font-size:.6rem;margin:5px 0 0;width:90px}.movie-item__your-rate{color:rgba(255,255,255,0.9);font-size:.9rem;width:90px;margin:0}.movie-item__your-rate--value{color:rgba(17,214,221,0.8)}.movie-item-rating{background-color:rgba(0,0,0,0.8);color:#fff;font-size:.8rem;margin:0;padding:3px 0;position:absolute;left:0;top:.2rem;text-align:center;width:100%}

.discover-item{position:relative}.discover-item__image{height:200px;border-radius:10px;border:0.7px solid #42a5f5;opacity:0.6;transition:200ms}.discover-item :hover{opacity:1}.discover-item__rating{position:absolute;left:1px;top:0;background:#ccc;color:#1C262B;padding:5px;opacity:0.8}.discover-item__title{color:#ccc;max-width:130px;text-align:center}

.discover-container{background:#1C262B}.discover-container h2{text-align:center;padding-top:20px}.discover-container-header{display:flex;flex-direction:column;max-width:80%;margin:0 auto;padding:20px;background:linear-gradient(150deg, rgba(40,207,219,0.142) 30%, rgba(52,238,238,0.342) 62%, rgba(13,150,204,0.142) 92%);border-radius:20px}.discover-container-header form{margin:0 auto}.discover-container-header select,.discover-container-header input{background-color:#445d69;border:none;border-radius:5px;color:#fff;padding:0.3rem}.discover-container-header button{height:32px;width:50%;margin:0 auto;border-radius:10px;color:rgba(219,69,69,0.9);background:#000;font-family:"Roboto", sans-serif;font-weight:700;letter-spacing:6.5px;cursor:pointer}.discover-container-header__filters{width:250px;display:flex;flex-direction:column;justify-content:space-around;height:120px}.discover-container-body{display:grid;grid-template-columns:repeat(auto-fit, 160px);grid-gap:4rem;justify-items:center;justify-content:center;padding-top:50px;border-radius:15px}.discover-container-body a{text-decoration:none}

