.backdrop {
	width: 100%;
	height: 100%;
	position: fixed;
	z-index: 100;
	left: 0;
	top: 0;
	background-color: rgba(22, 21, 21, 0.97);
}

.modalContent {
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
	top: 25%;
	width: 1000px;
	height: 562px;
	color: #fff;
	text-align: center;
}

@media only screen and (max-width: 1024px) {
	.modalContent {
		width: 100%;
		height: 0;
		padding-top: 55%;
	}
}

.modalContent iframe {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
}

.closeModal-btn {
	position: absolute;
	right: 10px;
	top: -100px;
	height: 50px;
	width: 50px;
	background-size: 50px 50px;
	background-position: center center;
	background-repeat: no-repeat;
	border-radius: 25px;
	border: none;
	cursor: pointer;
	outline: none;
	background-image: url("data:image/png;base64,");
}
