@import "../../styles/variables";

div {
	color: #fff;
}

.movie-box {
	position: relative;
	height: 75vh;
	width: 100%;
	font-family: $primary-font;

	&-info {
		padding: 40vh 0 0 33px;
	}
}

.movie-description {
	margin: 15px auto;
	padding: 20px;
	background: $tertiary-color;
	border-radius: 10px;
	border: 2px solid rgba(219, 69, 69, 0.40);
	font-family: $primary-font;

	h4 {
		margin: 0 0 10px 0;
		text-align: center;
	}
}

.go-homepage-button--svg {
	position: absolute;
	top: 40px;
	left: 50px;
	height: 50px;
	width: 50px;
	background-color: rgba(90, 91, 94, 0.5);
	background-position: center;
	background-size: 40px 45px;
	border-radius: 100%;
	box-shadow: 0px 0px 25px 2px rgb(252, 251, 251);
	background-image: url("data:image/png;base64,");
	will-change: background-image;

	&:hover {
		cursor: pointer;
		background-image: url("data:image/png;base64,");
	}
}
@media only screen and (max-width: 600px) {
	.movie-description {
		margin: 5px auto;
		padding: 7px;
	}
}
